:root {
  /* Core Data */
  --border-radius: 3;
  --size-checkbox-radio: 1.3125;

  --color-alert: rgb(255, 48, 48);
  --color-error: rgb(255, 176, 0);
  --color-caution: rgb(248, 231, 29);
  --color-ok: rgb(91, 255, 0);
  --color-standby: rgb(128, 224, 255);
  --color-off: rgb(198, 204, 209);

  --color-ui-font: #fff;

  /* Typography */
  --font-family: "Open Sans", sans-serif;
  --font-size: 1em; /* 16px */
  --font-ui-size: 1.125rem;
  --font-ui-color: #fff;
  --font-color: #fff;

  --font-family-light: "Open Sans Light", sans-serif;
  --font-family-mono: "Roboto Mono", monospace;

  /* UI Elements */
  --ui-element-disabled-opacity: 0.4;
  --ui-element-disabled-cursor: not-allowed;

  /* Buttons */
  --button-border-radius: calc(var(--border-radius) * 1px);
  --button-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);

  --button-background--hover: var(--color-tertiary, rgb(4, 124, 220));
  --background: var(--color-secondary);
}
