/*
**
** 	ASTRO BUTTON
** 	==========================================================================
**  2.1 Notes
**  - Removed Master Off Button Style
**  - Replaced various properties with css custom properties to support
**	1.4 Notes
**	- Added rux_ and BEM compatible classes to all satcom_ NOTE: satcom_ will be removed in a future version
**	- Disabled user selection of text on all buttons
**	- Removed redundant background hover from 'disabled' state by using :not() on the :hover state
** 	- Removed redundant background hover from 'master off' by using :not() on the :hover state // deprecate after 1.4
**  - Fixed Firefox alignment issue where text was misaligned vertically
**  - Renamed half-height to short and half-width to narrow (Note: rux_ only, satcom_ retains old syntax)
**  - Removed user-select and placed it in astro.css to apply to all input types
**  - Embedded master off icon and removed the additional states required to handle icons and gradient backgrounds
*/

.rux-button,
.satcom-button {
  display: inline-block;
  position: relative;

  margin: 0;
  padding: 0.1rem 1.5rem;

  height: 2.1875rem;
  min-width: 5rem;

  border: 1px solid var(--test, rgb(0, 90, 143));
  border-radius: var(--button-border-radius, "3px");

  background-color: var(--color-primary, rgb(0, 90, 143));

  color: var(--ui-font-color, #fff);
  font-family: var(--font-family);
  font-size: var(--ui-font-size, 1.125rem);

  white-space: nowrap;
  line-height: 1.5;

  box-shadow: var(--button-box-shadow, 0 1px 2px rgba(0, 0, 0, 0.5));
}

.rux-button--alt {
  color: var(--color-primary);
  background-color: var(--color-white, #fff);
}

/* disabled state */
.rux-button:disabled,
.satcom-button:disabled {
  opacity: var(--ui-element-disabled-opacity, 0.4);
  cursor: var(--ui-element-disabled-cursor, not-allowed);
}

.rux-button:focus,
.satcom-button:focus {
  outline: none;
}

/* hover state */
.rux-button:hover:not([disabled]):not(.rux-button--alt),
.satcom-button:hover:not([disabled]) {
  border-color: var(--color-tertiary, rgb(77, 172, 255));

  background-color: var(--color-tertiary, rgb(77, 172, 255));
}

.rux-button--alt:hover {
  color: var(--color-primary, rgb(0, 90, 143));

  background-color: var(--color-white, #fff);
}

/* down/press state */
.rux-button:active:not([disabled]):not(.rux-button--alt),
.satcom-button:active:not([disabled]) {
  border-color: var(--color-primary);
  background-color: var(--color-primary);
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.8);
}

/* Default Buttons */
.rux-button--default,
.satcom-button-default {
  border: 1px solid #fff;
}

.rux-icon {
  fill: #fff;
}

/* Smaller, half-width satcom-button */
.rux-button--narrow,
.satcom-button-half-wide {
  min-width: 3.125rem;

  padding-left: 0.3125rem;
  padding-right: 0.3125rem;
}

/* Smaller, half-height satcom-button */
.rux-button--short,
.satcom-button-half-tall {
  min-width: 6.25rem;

  padding-top: 0.1rem;
  padding-bottom: 0.1rem;
}

/* Perhaps a --compact class rather than explicit narrow and short classes? */
.rux-button--small {
  height: auto; /* remove in 2.0 */
  min-width: 64px;

  padding: 0.1em 0.5em;

  font-size: 0.775rem;
}

.rux-button--large {
  height: 2.8125rem;

  padding: 0.125em 1.25em;
  margin: 0;

  font-size: 1.125rem;
}

/* Version 2 "Master Off" would utilize the __icon element */
.rux-button__icon {
  display: inline-block;
  vertical-align: middle;

  height: 1.125rem;
  width: 1.125rem;

  margin-right: 0.175rem;
}

.rux-button--small .rux-button__icon {
  height: 0.875rem;
  width: 0.875rem;

  margin-right: 0.2em;
}

.rux-button--large .rux-button__icon {
  height: 1.8rem;
  width: 1.8rem;

  margin: 0;
  margin-right: 0.2em;
}
