/*
**
** 	ASTRO SLIDER
** 	==========================================================================
**  2.1 Notes
**  - Added support for bifurcated range slider
**	1.4 Notes
**	- Added rux_ compatible classes to all satcom_ NOTE: satcom_ will be removed in a future version
**	- Removed prefixed linear gradients
**  - Removed focus ring from Chrome (probably need an actual focus solution)
**  - Updated SVG
*/

.rux-range,
.satcom-range {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 10px 0;
  background: none;
}

input[type="range"]:focus {
  outline: none;
}

.rux-range::-webkit-slider-thumb,
.satcom-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 18px;
  width: 16px;

  background-image: url("img/range_thumb.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;

  margin-top: -7px;

  cursor: pointer;

  filter: drop-shadow(0 2px 0 rgba(0, 0, 0, 0.66));
}

.rux-range::-webkit-slider-thumb:active,
.satcom-range::-webkit-slider-thumb:active {
  filter: none;
}

.rux-range::-moz-range-thumb,
.satcom-range::-moz-range-thumb {
  -moz-appearance: none;
  height: 18px;
  width: 16px;
  background-color: transparent;

  background-image: url("img/range_thumb.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 -1px;
  border: none;

  cursor: pointer;

  filter: drop-shadow(0 2px 0 rgba(224, 60, 60, 0.66));
}

.rux-range::-webkit-slider-thumb:hover,
.rux-range::-webkit-slider-thumb:focus,
.satcom-range::-webkit-slider-thumb:hover,
.satcom-range::-webkit-slider-thumb:focus {
  background-position-x: -17px;
}

.rux-range::-moz-range-thumb:hover,
.rux-range::-moz-range-thumb:focus,
.satcom-range::-moz-range-thumb:hover,
.satcom-range::-moz-range-thumb:focus {
  background-position-x: -17px;
}

.rux-range::-moz-range-thumb:active,
.satcom-range::-moz-range-thumb:active {
  background-position-x: -34px;
}

.rux-range::-webkit-slider-thumb:active,
.satcom-range::-webkit-slider-thumb:active {
  background-position-x: -34px;
}

.rux-range:disabled::-moz-range-thumb,
.rux-range:disabled::-moz-range-thumb:active,
.rux-range:disabled::-moz-range-thumb:hover,
.rux-range:disabled::-moz-range-thumb:focus,
.satcom-range:disabled::-moz-range-thumb,
.satcom-range:disabled::-moz-range-thumb:active,
.satcom-range:disabled::-moz-range-thumb:hover,
.satcom-range:disabled::-moz-range-thumb:focus {
  cursor: not-allowed;
  background-position-x: -51px;
}

.rux-range:disabled::-webkit-slider-thumb,
.rux-range:disabled::-webkit-slider-thumb:active,
.rux-range:disabled::-webkit-slider-thumb:hover,
.rux-range:disabled::-webkit-slider-thumb:focus,
.satcom-range:disabled::-webkit-slider-thumb,
.satcom-range:disabled::-webkit-slider-thumb:active,
.satcom-range:disabled::-webkit-slider-thumb:hover,
.satcom-range:disabled::-webkit-slider-thumb:focus {
  cursor: not-allowed;
  background-position-x: -51px;
}

.rux-range:disabled::-webkit-slider-runnable-track,
.satcom-range:disabled::-webkit-slider-runnable-track {
  cursor: not-allowed;
  background: linear-gradient(
    to bottom,
    #416483 0%,
    #416483 59%,
    #405e7a 60%,
    #405e7a 100%
  );
}

.rux-range::-webkit-slider-runnable-track,
.satcom-range::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  cursor: pointer;

  background: #bdc3c9;

  background-image: linear-gradient(
    to right,
    #4dacff 0%,
    #4dacff calc(1% * var(--value)),
    #bdc3c9 calc(1% * var(--value)),
    #bdc3c9 100%
  );
}

.rux-range:disabled::-moz-range-track,
.satcom-range:disabled::-moz-range-track {
  cursor: not-allowed;
  background: linear-gradient(
    to bottom,
    #416483 0%,
    #416483 59%,
    #405e7a 60%,
    #405e7a 100%
  );
}

.rux-range::-moz-range-track,
.satcom-range::-moz-range-track {
  width: 100%;
  height: 4px;
  cursor: pointer;
  border-radius: 2px;
  background: #82bae6;
  background: linear-gradient(
    to bottom,
    #82bae6 0%,
    #82bae6 59%,
    #81a8c7 60%,
    #81a8c7 100%
  );
}

input[type="range"]::-moz-focus-outer {
  border: 0;
}
